//     sass- -  root
// colors
:root {
  // base
  --font-family: "Ubuntu", sans-serif;
  --content-width: 1230px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --color-light: #fff;
  --color-gray: #424551;
  --color-gray-600: #9A9CA5;
  --color-storm: #787A80;
  --color-ebony: #1E212C;
  --color-primary: #FF5A30;
  --color-primary-contrast: #FC3300;
  --color-lightgray: #F4F5F7;

  --grid-gap: 30px;
}
